<script>
export default {
  name: "editCompanyProfile-component",
  data() {
    return {
      s_id: "",
      form: {
        short_Name: "",
        SUPPLIER_CATEGORY: "",
        s_Name: "",
        TEL: "",
        address1: "",
        address2: "",
        key_Man: "",
        email: "",
        admin_Name: "",
        admin_Tel: "",
        admin_Id: "",
        pcomment: "",
      },
      rules: {
        short_Name: [{ required: true, message: "必填", trigger: "blur" }],
        SUPPLIER_CATEGORY: [
          { required: true, message: "必填", trigger: "change" },
        ],
        s_Name: [{ required: true, message: "必填", trigger: "blur" }],
        TEL: [{ required: true, message: "必填", trigger: "blur" }],
        address1: [{ required: true, message: "必填", trigger: "blur" }],
        address2: [{ required: true, message: "必填", trigger: "blur" }],
        key_Man: [{ required: true, message: "必填", trigger: "blur" }],
        tel2: [{ required: true, message: "必填", trigger: "blur" }],

        email: [{ required: true, message: "必填", trigger: "blur" }],
        admin_Name: [{ required: true, message: "必填", trigger: "blur" }],
        admin_Tel: [{ required: true, message: "必填", trigger: "blur" }],
        admin_Id: [{ required: true, message: "必填", trigger: "blur" }],
        pcomment: [{ required: false, message: "必填", trigger: "blur" }],
      },
    };
  },
  computed: {},
  methods: {
    // API取得資料
    async getSupplierDetail(id) {
      try {
        const getSupplierDetailRes = await this.$store.dispatch(
          "getSupplierDetail",
          id
        );
        this.form = getSupplierDetailRes.data.data;
      } catch (err) {
        console.log(err);
      }
    },
    goBack() {
      this.$router.go(-1);
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          await this.updSupplierDetail();
        } else {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    async updSupplierDetail() {
      try {
        await this.$store.dispatch("updSupplierDetail", this.form);
        this.$message({
          type: "success",
          duration: 5000,
          showClose: true,
          message: "更新成功",
        });
        this.getSupplierDetail(this.s_id);
        // this.$router.replace({ name: "companyProfileManagement" });
      } catch (err) {
        console.log(err);
      }
    },
  },
  async mounted() {
    this.s_id = this.$route.params.s_id;
    this.getSupplierDetail(this.s_id);
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./editCompanyProfile.html"></template>
<style lang="sass" scoped src="../../../../public/styles/custom.sass"></style>
